body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.Myapp{
  background: linear-gradient(#7b7b7b, #5ccab5);
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.form-container{
  background-color: #2c5046;
  height: 70vh;
  padding-top: 100px;
  padding-left: 50px;
  padding-right: 50px;
  margin: 60px auto;
}
input[type="text"]{
  background-color: transparent;
  border: 0;
  border-bottom:1px solid #fff ;
  width: 100%;
  display: block;
  padding-bottom: 4px;
  color: #fff !important;
  font-weight: lighter;
  letter-spacing: 2px;
  margin-bottom: 30px;
  margin-right: 20px;
  font-size: 20px;
  outline: 0;
}
button{
  border: 0;
  padding: 8px 20px;
  margin: 0 2px;
  border-radius:2px;
  font-weight: lighter;
  font-weight: 100;
  letter-spacing: 1px;
  font-size: 15px;
  cursor: pointer;
  background-color: #000;
  color: #fff;
}
.info{
  width: 100%;
  font-size: 20px;
  font-weight: 200;
  letter-spacing: 2px;
}
.info_p{
  color: #fff;
  border-bottom:2px solid rgba(255, 255, 255, 0.6);
  font-weight: 400;
}
.info_p:last-child{
  border: 0;
}
.info_val{
  color: #fff;
  font-weight: 200;
}